@import url(https://fonts.googleapis.com/css?family=Orienta|Ultra&display=swap);
body {
  background-color: rgba(248, 245, 245, 0.89); }

.main-title {
  display: flex;
  justify-content: center;
  margin: 1em 0 0.5em;
  color: #343434;
  font-family: Ultra, sans-serif;
  font-size: 36px;
  line-height: 42px;
  text-transform: uppercase;
  text-shadow: 0 3px white, 0 5px #777; }

.sub-title {
  display: flex;
  justify-content: center;
  color: #343434;
  font-family: Ultra, sans-serif; }

.load-btn {
  display: block;
  margin: 0 auto;
  cursor: pointer;
  outline: none;
  padding: 10px;
  border-radius: 10px;
  font-family: Orienta, sans-serif;
  font-size: 24px;
  font-weight: bold;
  background-color: white;
  border: 2px solid black; }
  .load-btn:hover {
    color: darkcyan;
    border-color: darkcyan; }

.posts-search {
  display: block;
  padding: 5px 10px;
  margin: 15px auto;
  border: 1px solid grey; }

.highlighted-text {
  background-color: green; }

.posts-container {
  padding: 0 40px; }

.post {
  box-shadow: inset 0 0 10px;
  background: white;
  color: #3f3e3e;
  display: flex;
  margin-bottom: 50px;
  padding: 40px; }
  .post__container {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin-right: 30px; }
  .post__information {
    background: url(data:image/jpeg;base64,);
    box-shadow: 0 0 10px;
    padding: 10px;
    margin-bottom: 30px; }
  .post__number, .post__title {
    text-align: center;
    margin: 0;
    color: #333;
    font-weight: normal;
    font-family: Orienta, sans-serif;
    font-size: 36px;
    line-height: 1.2; }
  .post__content {
    text-align: center;
    font-size: 24px; }
  .post__comments-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }

.user {
  background: url(data:image/jpeg;base64,);
  box-shadow: 0 0 10px;
  padding: 10px;
  text-align: center; }

.comment {
  width: 300px;
  margin: 0 20px 40px;
  padding: 10px;
  background: url(data:image/jpeg;base64,);
  box-shadow: 0 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly; }
  .comment__title {
    text-align: center; }

@media (max-width: 1024px) {
  .posts-container {
    padding: 0 20px; }
  .post {
    flex-direction: column; }
    .post__container {
      flex-direction: row;
      width: 100%;
      margin-right: 0;
      margin-bottom: 30px; }
    .post__information {
      padding: 10px;
      margin-bottom: 0;
      margin-right: 30px; }
    .post__comments-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: center; } }

@media (max-width: 768px) {
  .posts-container {
    padding: 0 15px; }
  .post {
    flex-direction: column; }
    .post__container {
      display: flex;
      flex-direction: column;
      margin-right: 0;
      margin-bottom: 30px; }
    .post__information {
      margin-bottom: 30px;
      margin-right: 0; }
    .post__number, .post__title {
      font-size: 24px; }
    .post__content {
      text-align: center;
      font-size: 16px; }
    .post__comments-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: center; } }

